export default {
	data() {
		return {
			showCategoryIndex:0,
			headerPosition:"fixed",
			searchKey:'',//搜索关键字
			categoryData:[],
			types:'normal',
			page:1,//页码
			activeKey: 0,
		}
	},
	onPageScroll(e){
		//兼容iOS端下拉时顶部漂移
		if(e.scrollTop>=0){
			this.headerPosition = "fixed";
		}else{
			this.headerPosition = "absolute";
		}
	},
	onLoad() {
		this.getCategoryInformation();
	},
	methods: {
		//获取分类信息
		getCategoryInformation(){
			let self = this;
			self.$axios(self.$baseUrl.getCategoryData,{})
			.then(res =>{
				if(res.data.code == 1){
					self.categoryData = res.data.data.categorydata;
					//默认获取第一个分类
					// self.showCategory(self.categoryData[0])
				}
				
			},(error) =>{
				console.log(error)
			})
		},
		//切换
		onChange(key) {
		  this.activeKey = key;
		},
		//分类列页面
		navToCategoryList(item){
			
			let id = item.id;
			let name = item.name;
			uni.navigateTo({
				url: `/pages/list/list?id=${id}&name=${name}`
			})
		},
		
		toCategory(e){
			//uni.showToast({title: e.name,icon:"none"});
			// uni.navigateTo({
			// 	url: '../goods/goods-list?cid='+e.id+'&name='+e.name
			// });
		},
		
		//搜索
		onSearch(){
			let name = this.searchKey;
			// 搜索页 
			uni.navigateTo({
				url: `/pages/list/list?name=${name}`
			})
		},
	}
}
